import React, { Component } from 'react';
export class Question extends React.Component {

  render() {
    let name =  "answer"+ this.props.col+ this.props.no;
    return (
      <div className="Question">
        <h3>{this.props.title}</h3>
        <label><input name={name} type="radio" value="1" onClick={this.props.setAnswer.bind(this,this.props.no+","+this.props.groupno+","+this.props.col+","+1)}/>Strongly Disagree</label>
        <label><input name={name} type="radio" value="2" onClick={this.props.setAnswer.bind(this,this.props.no+","+this.props.groupno+","+this.props.col+","+2)}/>Disagree</label>
        <label><input name={name} type="radio" value="3" onClick={this.props.setAnswer.bind(this,this.props.no+","+this.props.groupno+","+this.props.col+","+3)}/>Neutral</label>
        <label><input name={name} type="radio" value="4" onClick={this.props.setAnswer.bind(this,this.props.no+","+this.props.groupno+","+this.props.col+","+4)}/>Agree</label>
        <label><input name={name} type="radio" value="5" onClick={this.props.setAnswer.bind(this,this.props.no+","+this.props.groupno+","+this.props.col+","+5)}/>Strongly Agree</label>
      </div>
    );
  }

}

export default Question;



