import React, { Component } from 'react';
import $ from "jquery";
import logo from '../logo.png';
import '../App.css';
import {Question} from './Question';

let htmlResult = '';
let Col1 = [];
let Col2 = [];
let Col3 = [];
let Col4 = [];
let Col5 = [];
let Col6 = [];
let Col7 = [];
let Col8 = [];
let Col9 = [];
let Col10 = [];
let Col11 = [];
let Col12 = [];
let Col13 = [];
let Col14 = [];
let Col15 = [];
let Col16 = [];
let Results = {
  Col1: 0,
  Col2: 0,
  Col3: 0,
  Col4: 0,
  Col5: 0,
  Col6: 0,
  Col7: 0,
  Col8: 0,
  Col9: 0,
  Col10: 0,
  Col11: 0,
  Col12: 0,
  Col13: 0,
  Col14: 0,
  Col15: 0,
  Col16: 0
};

class App extends Component {
  constructor(props) {
    super(props);
    this.state = {
      result: '',
      resultText: ''
    };
  }
  showResults() {
    this.calculateResults();
    this.setState({
      result: 1,
      resultText: htmlResult
    })
  }
  showNextGroup(gno) {
    $(".group").hide();
    $(".Group"+gno).fadeIn(500);
    $('html,body').animate({ scrollTop: 0 }, 'slow');

  }
  calculateResults() {
    Col1.forEach(function(value){
          Results.Col1 = parseInt(Results.Col1) + parseInt(value)
    });
    Col2.forEach(function(value){
          Results.Col2 = parseInt(Results.Col2) + parseInt(value)
    });
    Col3.forEach(function(value){
          Results.Col3 = parseInt(Results.Col3) + parseInt(value)
    });
    Col4.forEach(function(value){
          Results.Col4 = parseInt(Results.Col4) + parseInt(value)
    });
    Col5.forEach(function(value){
          Results.Col5 = parseInt(Results.Col5) + parseInt(value)
    });
    Col6.forEach(function(value){
          Results.Col6 = parseInt(Results.Col6) + parseInt(value)
    });
    Col7.forEach(function(value){
          Results.Col7 = parseInt(Results.Col7) + parseInt(value)
    });
    Col8.forEach(function(value){
          Results.Col8 = parseInt(Results.Col8) + parseInt(value)
    });
    Col9.forEach(function(value){
          Results.Col9 = parseInt(Results.Col9) + parseInt(value)
    });
    Col10.forEach(function(value){
          Results.Col10 = parseInt(Results.Col10) + parseInt(value)
    });
    Col11.forEach(function(value){
          Results.Col11 = parseInt(Results.Col11) + parseInt(value)
    });
    Col12.forEach(function(value){
          Results.Col12 = parseInt(Results.Col12) + parseInt(value)
    });
    Col13.forEach(function(value){
          Results.Col13 = parseInt(Results.Col13) + parseInt(value)
    });
    Col14.forEach(function(value){
          Results.Col14 = parseInt(Results.Col14) + parseInt(value)
    });
    Col15.forEach(function(value){
          Results.Col15 = parseInt(Results.Col15) + parseInt(value)
    });
    Col16.forEach(function(value){
          Results.Col16 = parseInt(Results.Col16) + parseInt(value)
    });

    var list = {
        "Nurturer": (parseInt(Results.Col1) + parseInt(Results.Col9)),
        "Maverick": (parseInt(Results.Col2) + parseInt(Results.Col10)),
        "Alchemist": (parseInt(Results.Col3) + parseInt(Results.Col11)),
        "Celebrity": (parseInt(Results.Col4) + parseInt(Results.Col12)),
        "Connector": (parseInt(Results.Col5) + parseInt(Results.Col13)),
        "Accumulator": (parseInt(Results.Col6) + parseInt(Results.Col14)),
        "Romantic": (parseInt(Results.Col7) + parseInt(Results.Col15)),
        "Ruler": (parseInt(Results.Col8) + parseInt(Results.Col16))
      };

    htmlResult = Object.keys(list).sort(function(a,b){return list[a]-list[b]});


  }
  setAnswer(vtext) {
    var myans = vtext.split(",");
    if(myans[2] == "1") {
      Col1[parseInt(myans[0])-1] = myans[3];
    }
    else if(myans[2] == "2") {
      Col2[parseInt(myans[0])-1] = myans[3];
    }
    else if(myans[2] == "3") {
      Col3[parseInt(myans[0])-1] = myans[3];
    }
    else if(myans[2] == "4") {
      Col4[parseInt(myans[0])-1] = myans[3];
    }
    else if(myans[2] == "5") {
      Col5[parseInt(myans[0])-1] = myans[3];
    }
    else if(myans[2] == "6") {
      Col6[parseInt(myans[0])-1] = myans[3];
    }
    else if(myans[2] == "7") {
      Col7[parseInt(myans[0])-1] = myans[3];
    }
    else if(myans[2] == "8") {
      Col8[parseInt(myans[0])-1] = myans[3];
    }
    else if(myans[2] == "9") {
      Col9[parseInt(myans[0])-1] = myans[3];
    }
    else if(myans[2] == "10") {
      Col10[parseInt(myans[0])-1] = myans[3];
    }
    else if(myans[2] == "11") {
      Col11[parseInt(myans[0])-1] = myans[3];
    }
    else if(myans[2] == "12") {
      Col12[parseInt(myans[0])-1] = myans[3];
    }
    else if(myans[2] == "13") {
      Col13[parseInt(myans[0])-1] = myans[3];
    }
    else if(myans[2] == "14") {
      Col14[parseInt(myans[0])-1] = myans[3];
    }
    else if(myans[2] == "15") {
      Col15[parseInt(myans[0])-1] = myans[3];
    }
    else if(myans[2] == "16") {
      Col16[parseInt(myans[0])-1] = myans[3];
    }

  }
  renderQuiz() {
    return (
      <div>
          <div className="group Group1">
            <h2>Group 1</h2>
            <Question no="1" groupno="1" col="1" title="I am a generous person, often giving or loaning money to others"   setAnswer={this.setAnswer}/>
            <Question no="1" groupno="1" col="2" title="I like calculating the pros and cons of taking a financial risk"   setAnswer={this.setAnswer}/>
            <Question no="2" groupno="1" col="1" title="I often find myself coming to the aide of others, financially and otherwise"   setAnswer={this.setAnswer}/>
            <Question no="2" groupno="1" col="2" title="I am willing to risk money for a potentially large financial gain"   setAnswer={this.setAnswer}/>
            <Question no="3" groupno="1" col="1" title="Despite my best efforts, something often happens to complicate my life financially"   setAnswer={this.setAnswer}/>
            <Question no="3" groupno="1" col="2" title="I secretly feel clever or even superior to others about money"   setAnswer={this.setAnswer}/>
            <Question no="4" groupno="1" col="1" title="I find myself waiting to be paid because of someone else's needs or reasons"   setAnswer={this.setAnswer}/>
            <Question no="4" groupno="1" col="2" title="I like gaining the upper hand with money"   setAnswer={this.setAnswer}/>
            <button onClick={this.showNextGroup.bind(this,2)}>Next Group</button>
          </div>
          <div className="group Group2">
            <h2>Group 2</h2>
              <Question no="9" groupno="2" col="3"  title="I love attracting money in magical or unconventional ways"   setAnswer={this.setAnswer}/>
              <Question no="10" groupno="2" col="4"  title="I love spending money on things that enhance my image or give me a lot of visibility"   setAnswer={this.setAnswer}/>
              <Question no="11" groupno="2" col="3"  title="Making money isn't as important to me as creating a movement, making an impact or being involved in social change"   setAnswer={this.setAnswer}/>
              <Question no="12" groupno="2" col="4"  title="Always getting the best or being first VIP seating, flying first class, getting the latest gadgets  is important to me"   setAnswer={this.setAnswer}/>
              <Question no="13" groupno="2" col="3"  title="I feel a love/hate relationship with money"   setAnswer={this.setAnswer}/>
              <Question no="14" groupno="2" col="4"  title="I project an image of wealth and success that often doesn't match my bank account balance"   setAnswer={this.setAnswer}/>
              <Question no="15" groupno="2" col="3"  title="I believe it's greedy or not spiritual to focus too much on money"   setAnswer={this.setAnswer}/>
              <Question no="16" groupno="2" col="4"  title="It's easier for me to spend money on things like jewelry, restaurants, vacations, etc. than it is for me to save money"   setAnswer={this.setAnswer}/>
            <button onClick={this.showNextGroup.bind(this,3)}>Next Group</button>
          </div>
          <div className="group Group3">
            <h2>Group 3</h2>
              <Question no="17" groupno="3" col="5"  title="I find it easy not to think about money"   setAnswer={this.setAnswer}/>
              <Question no="18" groupno="3" col="6"  title="I love saving money"   setAnswer={this.setAnswer}/>
              <Question no="19" groupno="3" col="5"  title="I often find myself being financially supported by others"   setAnswer={this.setAnswer}/>
              <Question no="20" groupno="3" col="6"  title="I find it difficult, even painful to spend money"   setAnswer={this.setAnswer}/>
              <Question no="21" groupno="3" col="5"  title="I often avoid facing money situations, hoping they will improve on their own"   setAnswer={this.setAnswer}/>
              <Question no="22" groupno="3" col="6"  title="I make sure to always live below my means"   setAnswer={this.setAnswer}/>
              <Question no="23" groupno="3" col="5"  title="I like it best when someone else takes care of money details for me"   setAnswer={this.setAnswer}/>
              <Question no="24" groupno="3" col="6"  title="I find it difficult to trust others when investing my money"   setAnswer={this.setAnswer}/>
            <button onClick={this.showNextGroup.bind(this,4)}>Next Group</button>
          </div>
          <div className="group Group4">
            <h2>Group 4</h2>
              <Question no="25" groupno="4" col="7"  title="I love frequently spending money on things because I feel I deserve them"   setAnswer={this.setAnswer}/>
              <Question no="26" groupno="4" col="8"  title="I never feel there's enough money and continue to create ways of making more"   setAnswer={this.setAnswer}/>
              <Question no="27" groupno="4" col="7"  title="I don't see the point in saving money, as life is to be enjoyed now"   setAnswer={this.setAnswer}/>
              <Question no="28" groupno="4" col="8"  title="I am reluctant to diversify or complicate my investments"   setAnswer={this.setAnswer}/>
              <Question no="29" groupno="4" col="7"  title="Money itself doesn't feel that serious to me"   setAnswer={this.setAnswer}/>
              <Question no="30" groupno="4" col="8"  title="I forgo spending money on things now, in order to invest in my future"   setAnswer={this.setAnswer}/>
              <Question no="31" groupno="4" col="7"  title="I love having luxurious experiences or an abundance of items"   setAnswer={this.setAnswer}/>
              <Question no="32" groupno="4" col="8"  title="I am highly ambitious and see money as a measurement of my success"   setAnswer={this.setAnswer}/>
            <button onClick={this.showNextGroup.bind(this,5)}>Next Group</button>
          </div>
          <div className="group Group5">
            <h2>Group 5</h2>
              <Question no="33" groupno="5" col="9"  title="Being of service is more motivating than having more money"   setAnswer={this.setAnswer}/>
              <Question no="34" groupno="5" col="10"  title="At my most extreme the financial risks I take can put me in financial jeopardy"   setAnswer={this.setAnswer}/>
              <Question no="35" groupno="5" col="9"  title="I can feel resentful that I don't make more for all that I do"   setAnswer={this.setAnswer}/>
              <Question no="36" groupno="5" col="10"  title="I am comfortable with complex financial transactions"   setAnswer={this.setAnswer}/>
              <Question no="37" groupno="5" col="9"  title="If someone needs my time I'll often give it, even if I'm not being paid"   setAnswer={this.setAnswer}/>
              <Question no="38" groupno="5" col="10"  title="Making money is a game I play to win"   setAnswer={this.setAnswer}/>
              <Question no="39" groupno="5" col="9"  title="At my most extreme I give to the point of enabling or creating debt or putting myself at financial risk"   setAnswer={this.setAnswer}/>
              <Question no="40" groupno="5" col="10"  title="Others can view my financial decisions as risky"   setAnswer={this.setAnswer}/>
            <button onClick={this.showNextGroup.bind(this,6)}>Next Group</button>
          </div>
          <div className="group Group6">
            <h2>Group 6</h2>
              <Question no="41" groupno="6" col="11"  title="I believe people with money have unfair advantages in the world"   setAnswer={this.setAnswer}/>
              <Question no="42" groupno="6" col="12"  title="At my most extreme I create debt and spend lavishly in order to get attention and recognition"   setAnswer={this.setAnswer}/>
              <Question no="43" groupno="6" col="11"  title="I am distrustful of people who focus too much on money"   setAnswer={this.setAnswer}/>
              <Question no="44" groupno="6" col="12"  title="I am confident with day to day money management even though I may have debt or find it difficult to save or invest money"   setAnswer={this.setAnswer}/>
              <Question no="45" groupno="6" col="11"  title="Any investing I do needs to be with people or companies whose ideals match my own"   setAnswer={this.setAnswer}/>
              <Question no="46" groupno="6" col="12"  title="I love to stand out in the crowd and will use my purchases to help make that happen"   setAnswer={this.setAnswer}/>
              <Question no="47" groupno="6" col="11"  title="At my most extreme I am financially supported by someone (or something else, like a business, a spouse, a parent or a credit card) yet feel resentment or that the situation is unfair"   setAnswer={this.setAnswer}/>
              <Question no="48" groupno="6" col="12"  title="Being recognized for giving big to charities or social causes is important to me"   setAnswer={this.setAnswer}/>
            <button onClick={this.showNextGroup.bind(this,7)}>Next Group</button>
          </div>
          <div className="group Group7">
            <h2>Group 7</h2>
              <Question no="49" groupno="7" col="13"  title="I wish I didn't have to think about making or managing money"   setAnswer={this.setAnswer}/>
              <Question no="50" groupno="7" col="14"  title="At my most extreme I limit my opportunities because of my fear of spending or investing money"   setAnswer={this.setAnswer}/>
              <Question no="51" groupno="7" col="13"  title="I have faith that somehow, things will always work out financially"   setAnswer={this.setAnswer}/>
              <Question no="52" groupno="7" col="14"  title="I love buying things on sale, closeouts or used items so I know I'm getting the best deal"   setAnswer={this.setAnswer}/>
              <Question no="53" groupno="7" col="13"  title="I don't feel a strong connection to money"   setAnswer={this.setAnswer}/>
              <Question no="54" groupno="7" col="14"  title="Saving a lot of money makes me feel safe and protected"   setAnswer={this.setAnswer}/>
              <Question no="55" groupno="7" col="13"  title="At my most extreme I feel overwhelmed or even helpless about money and wish the need for money would just disappear"   setAnswer={this.setAnswer}/>
              <Question no="56" groupno="7" col="14"  title="I feel a strong emotional connection to money"   setAnswer={this.setAnswer}/>
            <button onClick={this.showNextGroup.bind(this,8)}>Next Group</button>
          </div>
          <div className="group Group8">
            <h2>Group 8</h2>
              <Question no="57" groupno="8" col="15"  title="I believe in living the good life, no matter what it costs"   setAnswer={this.setAnswer}/>
              <Question no="58" groupno="8" col="16"  title="At my most extreme I am never satisfied with how much money I have and drive myself relentlessly to create more"   setAnswer={this.setAnswer}/>
              <Question no="59" groupno="8" col="15"  title="I figure since you can't take it with you, money is to be spent now"   setAnswer={this.setAnswer}/>
              <Question no="60" groupno="8" col="16"  title="I am typically decisive regarding money and its management"   setAnswer={this.setAnswer}/>
              <Question no="61" groupno="8" col="15"  title="My purchases make me feel better about myself"   setAnswer={this.setAnswer}/>
              <Question no="62" groupno="8" col="16"  title="I believe I'll be happy when I have more money (even though what  I have now is more than I've had before)"   setAnswer={this.setAnswer}/>
              <Question no="63" groupno="8" col="15"  title="At my most extreme, I spend money impulsively"   setAnswer={this.setAnswer}/>
              <Question no="64" groupno="8" col="16"  title="I fear losing control of or power over my money"   setAnswer={this.setAnswer}/>
              <button onClick={this.showResults.bind(this)}>Generate Results</button>
          </div>
        </div>
    );
  }
  renderResult() {
    return (
      <div className="results">
        <h2>Congratulation</h2>
        <h3>Your Sacred Money Archetype Scores Are From Highest to Lowest:</h3>
        {this.state.resultText.map((hobby,i)=><li key={i} className="listitem">{hobby}</li>)}

      </div>
    );
  }
  render() {
    return (
      <div className="App">
        <div className="App-header">
          <img src={logo} className="App-logo" alt="logo" />
        </div>
        {this.state.result ? this.renderResult() : this.renderQuiz()}
      </div>
    );
  }

}

export default App;



